import React ,{Component} from 'react';
import {
    Form,
    Input,
    Button,
    Breadcrumb,
    Row,
    Modal 
} from 'antd';
import axios from 'axios'
class AddPingForm extends Component{
      constructor(props){
        super(props)
      }
  componentDidMount(){
    var key=sessionStorage.getItem('key')
    console.log(key);
  }

  
      handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            console.log(' ', values);
            var key=sessionStorage.getItem('key')
            var loginid=localStorage.getItem('loginid');
            var user=localStorage.getItem('name');
            console.log(key);
            if(loginid!='' && user!=""){
                console.log(key);
                axios.post('/index.php/zhi/ping',{
                  bid:key,
                  content:values.content,
                  name:user,
                  num:loginid
             }).then((res)=>{
                    if(res.data.code==200){
                     console.log('')
                      Modal.success({
                        title: '',
                        content: '',
                      });
                   }else{
                    Modal.error({
                      title: '',
                      content: '',
                    });
                   }
             }).catch((error)=>{
                   console.log(error)
             })
            }
        }

    })
}


editping=()=>{
   this.props.history.push('/projectmanage/projectInformation/editping')
}


      render(){
        const { getFieldDecorator } = this.props.form;
        const { TextArea } = Input;
        return(
          <div className="ping">
              <div className="bread">
                <Breadcrumb>
                    <Breadcrumb.Item href="/"> </Breadcrumb.Item>
                    <Breadcrumb.Item href="">  </Breadcrumb.Item>
                    <Breadcrumb.Item href=""> </Breadcrumb.Item>
                    <Breadcrumb.Item href=""> </Breadcrumb.Item>
                </Breadcrumb>
            </div>
            <Form layout="inline" onSubmit={this.handleSubmit}>
        <br/>
        <Row>
            <p className="title2"></p>
            <br/>
            <Form.Item >
            {getFieldDecorator('content', {
                })(
            <TextArea rows={10}   autosize={{ minRows: 10, maxRows: 50 }}  style={{width:1000}} placeholder="10"/>,
                )}
            </Form.Item >
        </Row>
        
      
        <br/>
        <Row>
            <Form.Item>
                <Button type="primary" style={{marginRight:800}}  onClick={this.editping}>
                 
                </Button>
                <Button type="primary" htmlType="submit" className="sub">
                
                </Button>
            </Form.Item>

        </Row>
      
          </Form>




          </div>
          )
      }
}
const AddPing=Form.create()(AddPingForm);
  export default AddPing